.content {
    min-width: 880px;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin: 0;
    padding: 0;
}

@media (max-width: 880px) {
    .content {
        min-width: 320px;
        margin: 0 auto;
    }
}